<script>
import tabbar from "@/components/tabbar.vue";
export default {
  data() {
    return {
      value: "",
      tab: 0,
      loading: false,
      finished: false,
      loading2: false,
      finished2: false,
      test: "关注成功",
      follow: false //是否关注
    };
  },
  components: {
    tabbar: tabbar
  },
  methods: {
    onSearch: function() {
      if (this.value) {
        this.$toast(this.value);
      } else {
        this.$toast("搜索文字不能为空！");
      }
    },
    qhtab(index) {
      //切换标签页
      this.$toast(index);
    },
    tozhubo() {
      //全部主播
      this.tab = 1;
    },
    tozb() {
      //全部直播
      this.tab = 2;
    },
    guanzhu(vid) {
      //关注
      this.$toast(vid);
      this.follow = true;
      this.$toast.success("关注成功");
    },
    onLoad() {
      //加载更多主播

      // 加载状态结束
      this.loading = false;
      // 数据全部加载完成
      this.finished = true;
    },
    onLoad2() {
      //加载更多直播间

      // 加载状态结束
      this.loading2 = false;
      // 数据全部加载完成
      this.finished2 = true;
    }
  },
  mounted() {
    this.value = this.$route.query.val;
    if (this.value) {
      this.onSearch();
    }
  }
};
</script>
